body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  background-color: #055302;
  display: -webkit-flex;
  display: flex;
  color: white;
  width: 100vw;
  height: 100vh;
  font-weight: 500;


}

.left {
  background-color: rgb(0, 117, 117);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-grow: 1;
          flex-grow: 1;

}

.right {
  background-color: rgb(89, 0, 172);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.App-editor {
  margin: 1rem;
  font-size: 1rem;
  min-height: 50vh;
  min-width: 50vw;
  color: white;
  background-color: #3b465a;
  border: 0.125rem solid;
  border-color: white;
}

.App-preview {
  margin: 1rem;
  font-size: 1rem;
  min-height: 50vh;
  min-width: 50vw;
  background-color: #3b465a;
  border: 0.125rem solid;
  border-color: white;
}

.App-title {
  margin: 2rem;
  font-size: 2rem;
  display: inline;
  font-weight: bold;
}



